@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;1,100;1,300;1,400;1,500&display=swap);
.garageH2 {
  text-align: center;
  padding: 24px 0;
}

.btn > .button {
  max-width: 40%;
}

div.btn {
  width: 100%;
}

.cards {
  display: grid;
  grid-template-columns: repeat(3);
  grid-auto-rows: auto;
  grid-gap: 1rem;
}

.garageCard {
  background-color: #28546be0;
  width: 70%;
  height: 300px;
  border-style: solid;
  border-color: #28536b;
  border-radius: 8px;
  border-width: 6px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  margin-bottom: 8px;
}

.garageImg > img {
  height: 42vh;
  width: 70vw;

  object-fit: cover;
}

.vNameDiv > h3 {
  padding-top: 8px;
  color: #FFF;
}

#headerQuote {
  color: #fff;
  text-align: center;
  padding: 20px 10px;
}

.loginBackground {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.65)),url(../../static/media/engine.75ea8778.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  height: 125vw;
}

/* Form styles */
form {
  display: inline-block;
  text-align: center;
}

.formContainer{
  margin: 0 auto;
  width: 75%;
  background-color: #FFF;
  padding: 16px 24px;
  border-radius: 4px;
  text-align: center;
}

label {
  margin: 8px 8px;
}

input {
  margin: 8px 0;
  padding: 8px 16px;
  background-color: rgb(230, 230, 230);
  border-radius: 4px;
  border: none;
}

.recordH2 {
  text-align: center;
  padding: 24px 0;
  }

  .recordCard {
    background-color: #28546be0;
    width: 70%;
    height: 225px;
    border-style: solid;
    border-color: #28536b;
    border-radius: 8px;
    border-width: 6px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    margin-bottom: 8px;
  }
  
  .recordImg > img {
    height: 30vh;
    width: 70vw;
  
    object-fit: cover;
  }

  .recordDiv {
    background-color: #1c7c543a;
    border-style: solid;
    border-color: #1c7c54cb;
    margin: 4px 8px;
    border-radius: 8px;
  }

  .pRecord {
    padding-top: 4px;
    padding-bottom: 4px;
  }

  .pRecord > p {
    margin: 0px 24px;
  }



.navbar {
    background-color: #28536B;
}
/* Global Styles */

body {
  font-family: "Roboto ", sans-serif;
}

.backOrSignOut {
  display: inline;
}

.back {
  float: left;
}

.signOutDiv {
  text-align: right;
  margin: 16px 24px;
}

h1 {
  font-family: "Roboto ", sans-serif;
  font-weight: 400;
  font-style: italic;
  font-size: 40px;
  text-align: center;
}

h2 {
  font-family: "Roboto ", sans-serif;
  font-weight: 400;
  font-style: italic;
  font-size: 32px;
  text-align: center;
  padding: 0 24px;
}

h3 {
  font-family: "Roboto ", sans-serif;
  font-weight: 400;
  font-style: italic;
  font-size: 24px;
  text-align: center;
  padding: 0 24px;
  color: #000;
  margin-top: auto;
  margin-bottom: auto;
}

/* button */

button {
  width: 50%;
  max-width: 50%;
  background-color: #1c7c54;
  color: white;
  padding: 4px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin: 16px auto;
  cursor: pointer;
}

#navBtn {
  background-color: rgb(142, 142, 142);
  color: #fff;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #1c7c54c6;
}

.submitDiv {
  text-align: center;
}

@media only screen and (min-width: 600px) {
  /* Styles for Desktop 600px wide + */
  div.btn {
    max-width: 40vw;
  }
  /* Styles for Record Page */
  .recordImg > img {
    height: 42vh;
    max-height: 43vh;
    width: 50vw;

    object-fit: cover;
  }
  .recordCard {
    background-color: #28546be0;
    width: 50vw;
    height: 250px;
    border-style: solid;
    border-color: #28536b;
    border-radius: 8px;
    border-width: 6px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    margin-bottom: 8px;
  }
  .recordDiv {
    background-color: #1c7c543a;
    border-style: solid;
    border-color: #1c7c54cb;
    margin: 16px auto;
    border-radius: 8px;
    width: 75vw;
  }
}

